import React from 'react';
import axios from 'axios';
import { SwaggerUI, LoginUI } from './views';
import { Navbar, Spinner, Alert } from './components';
import C from './constants';
function App() {
	const baseUrl = '/api/v1';
	const [isLoggedIn, setLogin] = React.useState(localStorage.getItem(C.SAFERIDE_API_DOC));
	const [loading, setLoading] = React.useState(false);
	const [error, setError] = React.useState(null);

	const login = (payload) => {
		setLoading(true);
		axios
			.post(baseUrl + '/auth/login', {
				...payload,
			})
			.then((res) => {
				setLoading(false);
				setLogin(res.data.token);
				localStorage.setItem(C.SAFERIDE_API_DOC, res.data.token);
			})
			.catch((e) => {
				setLoading(false);
				setError(e);
			});
	};
	const logout = () => {
		// setLoading(true);
		axios
			.delete(baseUrl + '/auth/logout', {
				headers: {
					Authorization: `bearer ${isLoggedIn}`,
				},
			})
			.then(() => {
				setLoading(false);
				setLogin(null);
				localStorage.removeItem(C.SAFERIDE_API_DOC);
			})
			.catch((e) => {
				setLoading(false);
				setError(e);
			});
	};
	const ui = isLoggedIn ? <SwaggerUI baseUrl={baseUrl} /> : <LoginUI handleLogin={login} />;
	return (
		<div>
			<Navbar handleSignOut={logout} isLoggedIn={isLoggedIn} />
			{ui}
			<div className="container">
				<div className="center-align" style={{ marginTop: 40 }}>
					{loading && <Spinner />}
					{error && Alert({ message: error.message, color: 'red' })}
				</div>
			</div>
		</div>
	);
}

export default App;
