import React from 'react';

export default ({ handleSignOut, isLoggedIn }) => {
	return (
		<nav>
			<div className="nav-wrapper blue-grey darken-3">
				<div className="container">
					<a href="#" className="brand-logo">
						Saferide
					</a>
					{isLoggedIn && (
						<ul className="right ">
							<li
								onClick={(e) => {
									e.preventDefault();

									handleSignOut();
								}}
							>
								<a href="#!">Sign Out</a>
							</li>
						</ul>
					)}
				</div>
			</div>
		</nav>
	);
};
